@charset "UTF-8";

/*
* Функция перевода пиксилей в реммы 
*/

/*
* Миксин адаптивного шрифта
*/

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

/*Обнуление*/

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*:focus,
*:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html {
  font-size: 10px;
}

html,
body {
  min-width: 320px;
  height: 100%;
}

body {
  font-feature-settings: "kern" 1, "liga" 1, "calt" 1, "pnum" 1, "tnum" 0, "onum" 0, "lnum" 0, "dlig" 0;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
  font-weight: inherit;
  font-size: inherit;
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a {
  color: inherit;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

body._lock {
  overflow: hidden;
}

body.lock-popup {
  overflow: hidden;
}

.js-form__message {
  display: none;
}

body {
  font-family: "OpenSans", sans-serif;
  color: #EDEDED;
  font-weight: 400;
  line-height: 1;
  background-color: #191A1E;
  font-size: 1.6rem;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.container {
  max-width: 156rem;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.page {
  flex: 1 1 auto;
}

.item-response img,
.item-response svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.js-padding {
  padding: 11rem 0 0 0;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.ie-fix {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.ie-fix:before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  z-index: 999;
  content: "БРАУЗЕР УСТАРЕЛ, ПОПРОБУЙТЕ ЛЮБОЙ ДРУГОЙ БРАУЗЕР";
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
}

._tabs-wrapper {
  position: relative;
}

._tabs-item {
  cursor: pointer;
}

._tabs-block {
  height: 0;
  transform: translate(25%, 0);
  transition: height 0.6s, transform 0.6s ease, visibility 0.6s, opacity 0.6s;
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 0;
  z-index: 4;
}

._tabs-block._active {
  z-index: 5;
  transform: translate(0, 0);
  top: 0;
  position: relative;
  opacity: 1;
  visibility: visible;
  transform: scale(1);
  transition: position 0.5s 0.1s, transform 1s 0.1s ease, visibility 0.1s 0.1s, opacity 1s 0.1s;
  height: auto;
}

._tabs__block {
  display: none;
}

._tabs__block._open {
  display: block;
}

._tabs__block {
  display: none;
}

._tabs__block._open {
  display: block;
}

.btn-wrapper {
  display: flex;
  justify-content: center;
}

._btn {
  height: 50px;
  max-width: 340px;
  width: 100%;
  padding: 0 10px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  color: #fff;
  white-space: nowrap;
  transition: color 0.5s;
}

._btn:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: blue;
}

._btn span {
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  border-radius: 50%;
  background-color: #053a97;
  transition: width 0.5s, height 0.5s;
  transform: translate(-50%, -50%);
  z-index: -1;
}

._btn svg {
  flex-shrink: 0;
  position: relative;
  z-index: 2;
  transition: transform 0.7s;
}

._btn svg path {
  transition: fill 0.7s;
}

._btn:active {
  transform: translateY(0.2rem);
}

.back-to-top {
  position: fixed;
  right: -60px;
  background: #1f60d1;
  border-radius: 50%;
  bottom: 15px;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 8;
  visibility: hidden;
  transition: right 0.7s, visibility 0.7s, background 0.7s;
}

.back-to-top:before {
  content: "";
  width: 14px;
  height: 14px;
  border: solid #fff;
  border-radius: 3px;
  border-width: 3px 3px 0 0;
  transform: rotate(-45deg);
  position: relative;
  top: 3px;
}

.back-to-top:active:before {
  top: 2px;
}

.back-to-top._show {
  visibility: visible;
  right: 5px;
}

.anim {
  transform: translate(0, 40px);
  opacity: 0;
  transition: transform 0.8s 0.3s, opacity 0.8s 0.3s;
}

.anim._active {
  transform: translate(0, 0);
  opacity: 1;
  transition: transform 0.5s 0.1s, opacity 0.5s 0.1s;
}

.underline {
  position: relative;
}

.underline:before {
  content: "";
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
  transition: 0.5s transform;
  background-color: #fff;
}

@keyframes shake {
  0%, 50%, 100% {
    transform: rotate(0deg);
  }

  10%, 30% {
    transform: rotate(-10deg);
  }

  20%, 40% {
    transform: rotate(10deg);
  }
}

html {
  font-size: 8px;
}

.form__items {
  margin: 0 0 15px 0;
}

.form__item {
  position: relative;
  display: block;
}

.form__input {
  display: block;
  padding: 10px 0 17px 0;
  font-size: 16px;
  border-bottom: 1px solid;
  border-color: #181818;
  width: 100%;
}

.form__input::placeholder {
  color: #181818;
}

.form textarea {
  max-height: 47px;
  resize: none;
}

.reference-form__error {
  font-size: 13px;
  position: absolute;
  top: calc(100% + 1px);
  left: 0;
  opacity: 0;
  transition: opacity 0.7s, transform 0.7s;
  transform: translate(0, -5px);
  color: red;
  z-index: 2;
}

.reference-form__input-container--error .checkbox__check {
  border-color: red;
}

.reference-form__input-container--error .reference-form__error {
  opacity: 1;
  transform: translate(0, 0);
}

.reference-form__input-container--error .form__input {
  border-color: red;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.input {
  border-radius: 0 !important;
}

.checkbox {
  display: inline-flex;
  cursor: pointer;
  align-items: center;
}

.checkbox__input {
  display: none;
}

.checkbox__input:checked + .checkbox__check::after {
  transform: scale(1);
}

.checkbox__input:checked + .checkbox__check {
  border-color: #000;
}

.checkbox__check {
  position: relative;
  display: inline-block;
  align-self: flex-start;
  width: 20px;
  flex-shrink: 0;
  height: 20px;
  background-color: transparent;
  margin: 0px 10px 0px 0px;
  border: 1px solid;
  border-color: #000;
  transition: border-color 0.5s;
}

.checkbox::after {
  transition: transform 0.5s ease 0s;
  content: "";
  width: 14px;
  height: 14px;
  position: absolute;
  top: 2px;
  left: 2px;
  background-color: #000;
  transform: scale(0);
}

.checkbox__agree {
  line-height: 140%;
  position: relative;
  user-select: none;
}

.checkbox a {
  text-decoration: underline;
}

.header {
  height: 10rem;
  position: relative;
}

.header__wrapper {
  background-color: #000;
  color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9;
}

.header__body {
  display: flex;
  align-items: center;
  height: 10rem;
  transition: height .25s;
}

.header__body:before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 8rem;
  background-color: #000;
  z-index: 2;
}

.header__logo {
  margin: 0 5.3rem 0 0;
  width: 13.9rem;
  flex-shrink: 0;
  align-self: center;
  position: relative;
  z-index: 7;
  transition: opacity .25s;
}

.header__logo:active {
  opacity: 0.75;
  transition: opacity .15s;
}

.header__logo img,
.header__logo svg {
  width: 100%;
}

.header__nav {
  z-index: 4;
  transition: width 1s;
  width: 100%;
}

.header__nav.js-nav-hidden {
  width: 0;
  overflow: hidden;
  pointer-events: none;
}

.header__list {
  display: flex;
  gap: 3.2rem;
}

.header__list a {
  position: relative;
  transition: opacity .15s;
}

.header__list a:active {
  opacity: 0.75;
}

.header__list a::after {
  content: '';
  position: absolute;
  bottom: -0.2rem;
  left: 0;
  width: 100%;
  height: .1rem;
  background: #fff;
  transform: scaleX(0);
  transition: .25s transform;
  pointer-events: none;
}

.header__list > li {
  flex-shrink: 0;
}

.header__list > li > a {
  white-space: nowrap;
  font-size: 1.4rem;
  text-transform: uppercase;
  line-height: 1.3;
  position: relative;
  z-index: 2;
}

.header__list > li > a:after {
  bottom: -1.2rem;
}

.header__actions {
  display: flex;
  gap: 3.2rem;
  position: relative;
  z-index: 4;
  height: 2.4rem;
}

.header__actions-count {
  z-index: 2;
}

.header__actions-count a {
  position: relative;
}

.header__actions-count a span {
  position: absolute !important;
  top: -1.1rem;
  right: -1.8rem;
  border-radius: 2rem;
  background-color: #AD0C10;
  height: 2.5rem;
  min-width: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 1.4rem;
  padding: 0 0.6rem;
  z-index: 2;
  border: 0.2rem solid #000;
  transition: transform .25s;
}

.header__actions-count a span:after {
  display: none;
}

.header__actions-count a img,
.header__actions-count a svg {
  width: 2.4rem;
  height: 2.4rem;
  z-index: 1;
  position: relative;
}

.header__user > a {
  display: flex;
  align-items: center;
  gap: 1rem;
  position: relative;
  z-index: 2;
  transition: opacity .15s;
}

.header__user > a:active {
  opacity: 0.75;
}

.header__user > a img,
.header__user > a svg {
  width: 2.4rem;
  height: 2.4rem;
  transition: opacity .25s;
}

.header__user > a span {
  max-width: 11rem;
  overflow: hidden;
  text-overflow: ellipsis;
  text-transform: uppercase;
  font-size: 1.4rem;
}

.header__user-exit {
  position: relative;
  z-index: 2;
  align-self: flex-start;
  flex-shrink: 0;
}

.header__user-exit a {
  width: 2.4rem;
  height: 2.4rem;
}

.header__user-exit a svg {
  width: 100%;
  height: 100%;
}

.header__user-exit a svg path {
  transition: fill .25s;
}

.header__user .submenu__wrapper {
  left: auto;
  right: 0;
  display: flex;
  gap: 0.8rem;
}

.header__user .submenu__wrapper ul a {
  text-transform: uppercase;
  font-weight: 500 !important;
  position: relative;
  font-size: 1.4rem;
  white-space: nowrap;
  transition: opacity .15s;
}

.header__user .submenu__wrapper ul a:active {
  opacity: 0.75;
}

.header__user .submenu__wrapper ul a::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: .1rem;
  background: #fff;
  transform: scaleX(0);
  transition: .25s transform;
  pointer-events: none;
}

.submenu.js-submenu-open .submenu__wrapper {
  pointer-events: all;
  opacity: 1;
  transform: translateY(0);
  visibility: visible;
}

.submenu.js-submenu-open > a:after {
  transform: scaleX(1);
}

.submenu--big.js-submenu-open:before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 8rem;
  background-color: #000;
  z-index: 2;
}

.submenu:not(.submenu--big) {
  position: relative;
}

.submenu:not(.submenu--big) .submenu__wrapper {
  padding: 0;
  width: auto;
  overflow: visible;
  padding: 8rem 0 3rem;
}

.submenu:not(.submenu--big) .submenu__wrapper:before {
  display: none;
}

.submenu:not(.submenu--big) .submenu__wrapper ul:before {
  content: '';
  position: absolute;
  top: 0;
  left: -3.3rem;
  width: calc( 100% + 6.6rem);
  height: 100%;
  background-color: #000;
  z-index: 0;
}

.submenu:not(.submenu--big) .submenu__wrapper li {
  white-space: nowrap;
  position: relative;
  z-index: 1;
  margin: 0 0 2rem 0;
}

.submenu:not(.submenu--big) .submenu__wrapper li:last-child {
  margin: 0;
}

.submenu:not(.submenu--big) .submenu__wrapper a {
  font-weight: 400;
}

.submenu__wrapper {
  background-color: #000;
  position: absolute;
  width: 100%;
  padding: 13.5rem 0 0;
  top: 0;
  left: 0;
  z-index: 1;
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.5s, transform 0.5s, visibility .5s;
  transform: translateY(-30px);
  overflow: auto;
  max-height: 85vh;
}

.submenu__step-back {
  background-color: transparent;
  width: 2.2rem;
  height: 2.2rem;
  margin: 0 1.6rem 2rem;
  display: block;
  position: relative;
  z-index: 2;
}

.submenu__step-back img,
.submenu__step-back svg {
  width: 100%;
  height: 100%;
}

.sublist {
  position: relative;
  min-height: 48rem;
  padding-bottom: 3rem;
}

.sublist__item {
  padding: 0 0 2rem 0;
  max-width: 34rem;
}

.sublist__item:last-child {
  padding: 0;
}

.sublist__item.js-sublist-open .sublist__wrapper {
  pointer-events: all;
  opacity: 1;
}

.sublist__item.js-sublist-open > a:after {
  transform: scaleX(1);
}

.sublist__item > a {
  text-transform: uppercase;
  z-index: 1;
}

.sublist__wrapper {
  display: flex;
  justify-content: space-between;
  position: absolute;
  gap: 4rem;
  top: 0;
  left: 39rem;
  width: calc(100% - 43rem);
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.5s;
  padding: 0 0 3.5rem 0;
}

.sublist__wrapper:before {
  content: '';
  position: fixed;
  top: -3rem;
  left: -35rem;
  width: calc(100% + 35rem);
  height: calc( 100% + 3rem);
  z-index: 0;
}

.sublist__wrapper > ul {
  width: 33.333%;
}

.sublist__wrapper > ul li {
  margin: 0 0 2rem 0;
}

.sublist__wrapper > ul li:last-child {
  margin: 0;
}

.sublist__wrapper > ul li a {
  font-weight: 400;
  transition: opacity 0.25s;
}

.sublist__wrapper > ul li a:after {
  display: none;
}

.sublist__image {
  width: 33.333%;
  min-width: 20rem;
  align-self: flex-start;
}

.sublist__image img {
  width: 100%;
  max-width: 100%;
}

.menu-burger {
  display: block;
  width: 1.8rem;
  height: 2.2rem;
  position: relative;
  z-index: 5;
  background-color: transparent;
  flex-shrink: 0;
  margin: 0 2.5rem 0 0;
}

.menu-burger.js-menu-open:before {
  transform: rotate(-45deg);
  top: 1rem;
}

.menu-burger.js-menu-open:after {
  transform: rotate(45deg);
  bottom: 1rem;
}

.menu-burger:before,
.menu-burger:after {
  content: '';
  position: absolute;
  left: 0;
  width: 1.8rem;
  height: 0.2rem;
  background-color: #fff;
  border-radius: 8rem;
  transition: transform .3s, top .3s, bottom .3s;
}

.menu-burger:before {
  top: 0.6rem;
}

.menu-burger:after {
  bottom: 0.6rem;
}

.menu-mobile {
  display: block;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  padding: 12rem 0 3rem;
  pointer-events: none;
  transform: translateX(-100%);
  transition: transform .7s;
}

.menu-mobile.js-menu-open {
  pointer-events: all;
  transform: translateX(0);
  overflow: auto;
}

.menu-mobile.js-menu-open._lock {
  overflow: hidden;
}

.menu-mobile__list {
  display: block;
}

.menu-mobile__list > li {
  margin: 0 0 2rem 0;
}

.menu-mobile__list > li:last-child {
  margin: 0;
}

.menu-mobile__list > li > a {
  font-size: 1.5rem;
}

.menu-mobile__list .submenu:not(.submenu--big) .submenu__wrapper {
  padding: 0;
}

.menu-mobile__list .submenu:not(.submenu--big) .submenu__wrapper ul {
  padding: 2rem 0 3rem;
}

.menu-mobile__list .submenu__wrapper {
  position: relative;
  display: none;
  pointer-events: all;
  opacity: 1;
  transform: translateY(0);
  visibility: visible;
}

.menu-mobile__list .submenu__wrapper ul:before {
  display: none;
}

.menu-mobile__list ._spoller {
  width: 100%;
  position: relative;
  transition: opacity .25s;
  display: block;
}

.menu-mobile__list ._spoller._active {
  opacity: 0.75;
}

.menu-mobile__list ._spoller._active:before {
  transform: translateY(-50%) rotate(0deg);
}

.menu-mobile__list ._spoller._active:after {
  transform: scale(1) translateY(-50%) rotate(180deg);
}

.menu-mobile__list ._spoller:before,
.menu-mobile__list ._spoller:after {
  content: '';
  position: absolute;
  width: 1.4rem;
  height: 0.2rem;
  background-color: #fff;
  top: 50%;
  right: 0;
  transition: transform .5s;
}

.menu-mobile__list ._spoller:before {
  transform: translateY(-50%) rotate(-90deg);
}

.menu-mobile__list ._spoller::after {
  left: auto;
  transform: scale(1) translateY(-50%);
}

.menu-mobile__list .submenu--big.js-submenu-open > a {
  opacity: 0.75;
}

.menu-mobile__list .submenu--big.js-submenu-open .submenu__wrapper {
  transform: translateX(0);
  pointer-events: all;
}

.menu-mobile__list .submenu--big > a {
  width: 100%;
  position: relative;
  transition: opacity .25s;
}

.menu-mobile__list .submenu--big > a:before,
.menu-mobile__list .submenu--big > a:after {
  content: '';
  position: absolute;
  width: 1.1rem;
  height: 0.2rem;
  background-color: #fff;
  right: 0;
  transition: transform .5s;
}

.menu-mobile__list .submenu--big > a:before {
  top: 0.5rem;
  transform: rotate(45deg);
}

.menu-mobile__list .submenu--big > a::after {
  bottom: 0.6rem;
  left: auto;
  transform: scale(1) rotate(-45deg);
}

.menu-mobile__list .submenu--big .submenu__wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 8;
  max-height: 100vh;
  display: block;
  transform: translateX(200%);
  transition: transform .5s, opacity 0.3s;
  pointer-events: none;
}

.search-header {
  flex: 1 1 auto;
  margin: 0 1.5rem;
  padding: 0 1.5rem;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 5;
  height: 5.5rem;
  background-color: #000;
  transition: transform .5s;
}

.search-header.js-search-open {
  z-index: 8;
}

.search-header.js-search-open .search-header__form {
  display: block;
}

.search-header.js-search-open .search-header__result {
  display: block;
}

.search-header.js-search-open .search-header__icon--close {
  pointer-events: all;
  width: 2.4rem;
